.promo {
  background-color: $color-default-white;
  color: $color-dark;
  padding-top: 120px;
  padding-bottom: 55px;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 50px;
  }

  @include vp-767 {
    padding-top: 70px;
    padding-bottom: 35px;
  }
}

.promo__wrapper {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 100%;

  @include vp-767 {
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}

.promo__wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.promo__inner {
  background-color: #ed0233;
  color: $color-default-white;
  padding: 87px 78px;
  display: grid;
  grid-template-columns: repeat(2, auto);

  @include vp-1199 {
    padding: 51px 45px;
  }
}

.promo__inner h2 {
  font-family: "Alumni Sans", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 82px;
  line-height: 79px;
  text-transform: uppercase;
  grid-column: 1;
  margin-right: 50px;

  @include vp-1199 {
    font-size: 48px;
    line-height: 46px;
    margin-right: 30px;
  }
}

.promo__inner p {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  grid-column: 1/-1;
  margin-bottom: 30px;

  @include vp-1199 {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 20px;
  }
}

.promo__inner a {
  grid-column: 1/-1;
  align-self: flex-end;

  @include vp-1199 {
    font-size: 10px;
    line-height: 12px;
    border-width: 2px;
  }
}

.promo__list {
  grid-column: 2;
  display: flex;
  flex-direction: column;
}

.promo__item {
  display: flex;
  flex-direction: column;
}

.promo__item span {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;

  @include vp-1199 {
    font-size: 12px;
    line-height: 18px;
  }
}

.promo__item b {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  display: flex;
  align-items: center;
  align-self: flex-start;
  border-top: 1px solid #ffffff;

  @include vp-1199 {
    font-size: 24px;
    line-height: 36px;
  }
}

.promo__item small {
  font-family: "Roboto Condensed", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  margin-left: 5px;

  @include vp-1199 {
    font-size: 10px;
    line-height: 15px;
  }
}
